<template>
  <section id="assets-manage-page" class="df">
    <el-tabs class="tabs df" v-model="curView">
      <el-tab-pane label="资产信息" name="1">
        <div class="tab-content df dfv">
          <div class="query-form df">
            <el-form :inline="true" :label-width="'100px'" style="max-width:1500px;">
              <el-form-item :label="'地区'" prop="district">
                <el-input size="mini" v-model="assetsQuery.district" style="width: 220px;"></el-input>
              </el-form-item>

              <el-form-item :label="'资产ID'" prop="id">
                <el-input size="mini" v-model="assetsQuery.id" style="width: 220px;"></el-input>
              </el-form-item>

              <el-form-item :label="'资产类型'" prop="type">
                <el-select v-model="assetsQuery.type" size="mini" style="width: 220px;">
                  <el-option v-for="(item, index) in linearCITypeList" :key="index + 'relationship'" :label="item.title" :value="item.name" :disabled="item.level < 3">
                    <div :class="'option-level' + item.level">{{item.title}}</div>
                  </el-option>
                </el-select>
              </el-form-item>
              
              <el-form-item :label="'设备序列号'" prop="sn">
                <el-input size="mini" v-model="assetsQuery.sn" style="width: 220px;"></el-input>
              </el-form-item>
              
              <el-form-item :label="'创建时间'" prop="startCreateTime">
                <el-date-picker
                  style="width: 220px;"
                  v-model="assetsQuery.startCreateTime"
                  type="datetime"
                  size="mini"
                  placeholder="">
                </el-date-picker>
              </el-form-item>
              
              <el-form-item :label="'-　 　'" prop="endCreateTime">
                <el-date-picker
                  style="width: 220px;"
                  v-model="assetsQuery.endCreateTime"
                  type="datetime"
                  size="mini"
                  placeholder="">
                </el-date-picker>
              </el-form-item>
              
              <el-form-item :label="'修改时间'" prop="startModifyTime">
                <el-date-picker
                  style="width: 220px;"
                  v-model="assetsQuery.startModifyTime"
                  type="datetime"
                  size="mini"
                  placeholder="">
                </el-date-picker>
              </el-form-item>
              
              <el-form-item :label="'-　 　'" prop="endModifyTime">
                <el-date-picker
                  style="width: 220px;"
                  v-model="assetsQuery.endModifyTime"
                  type="datetime"
                  size="mini"
                  placeholder="">
                </el-date-picker>
              </el-form-item>
              
              <el-form-item :label="'状态'" prop="status">
                <el-input size="mini" v-model="assetsQuery.status" style="width: 220px;"></el-input>
              </el-form-item>

              <el-button type="primary" size="mini" @click="getAssetsList()" style="margin-left: 33px;">确认</el-button>
              <el-button type="primary" size="mini" @click="assetsQuery = {}">清除</el-button>
            </el-form>
          </div>
                
          <div class="list df dfv">
            <div class="list-header">
              <h3>告警周期列表</h3>
              <div class="btn-group">
                <el-button type='primary' size="mini" @click="addAssetToModal()">添加</el-button>
              </div>
            </div>
            <div class="table-box df dfv">
              <el-table height="100%" :data="assetsList">
                <el-table-column label="序号" type="index"></el-table-column>
                <el-table-column label="资产ID" width="450" prop="id"></el-table-column>
                <el-table-column label="资产类型" prop="title"></el-table-column>
                <el-table-column label="设备序列号" prop="sn"></el-table-column>
                <el-table-column label="创建时间" prop="createTime" :formatter="$tableFilter('dateFormatFilter')"></el-table-column>
                <el-table-column label="修改时间" prop="modifyTime" :formatter="$tableFilter('dateFormatFilter')"></el-table-column>
                <el-table-column label="状态" prop="status" :formatter="$tableFilter(['assetStatusFilter', 'nullFilter'])"></el-table-column>
                <el-table-column label="操作">
                  <template slot-scope="scope">
                    <label><a @click="topoToModal(scope.row)" title="查看" class="icomoon pic-look"></a></label>
                    <label><a @click="addAssetToModal(scope.row)" title="修改" class="icomoon pic-edit"></a></label>
                    <label><a @click="deleteAsset(scope.row)" title="删除" class="icomoon pic-delete"></a></label>
                  </template>
                </el-table-column>
              </el-table>
            </div>
            <!-- Pagination -->
            <el-col :span="24" class="toolbar">
              <el-pagination
                layout="total, prev, pager, next,sizes, jumper"
                @current-change="pagination.handleCurrentChange"
                @size-change="pagination.pageSizeChange"
                :page-sizes="[10, 15, 50, 100]"
                :page-size="pagination.pageSize"
                :total="pagination.total"
                style="float:right;">
              </el-pagination>
            </el-col>
          </div> 
        </div>
      </el-tab-pane>
      <el-tab-pane label="资产类型" name="2">
        <div class="tab-content df">
          <div class="left-tree tree">
            <tree :nodes="nodes" :setting="treeSetting" ref="tree" />
          </div>

          <div class="right-content df dfv">
            <div class="list-header">
              <h3>告警周期列表</h3>
              <div class="btn-group">
                <el-button v-if="deviceInfoForm.$type !== 'add'" type='primary' size="mini" @click="deleteDeviceInfo()">删除</el-button>
                <el-button type='primary' size="mini" @click="resetDeviceInfoForm()">重置</el-button>
                <el-button type='primary' size="mini" @click="addDeviceInfo()">{{ deviceInfoForm.$type === 'add' ? '添加' : '修改' }}</el-button>
              </div>
            </div>

            <el-form :model="deviceInfoForm" :rules="deviceInfoRules" label-width="90px" id="deviceInfo" ref="deviceInfo" style="padding: 20px 0;">
              <el-form-item label="英文名" prop="name" class="dib" style="vertical-align:top;">
                <el-input v-model="deviceInfoForm.name" size="mini" class="form-item"></el-input>
              </el-form-item>
              <el-form-item label="中文名" prop="title" class="dib" style="vertical-align:top;">
                <el-input v-model="deviceInfoForm.title" size="mini" class="form-item"></el-input>
              </el-form-item>
              <el-form-item label="类型编码" prop="code" class="dib" style="vertical-align:top;">
                <el-input :disabled="deviceInfoForm.$type !== 'add'" v-model="deviceInfoForm.code" size="mini" class="form-item"></el-input>
              </el-form-item>
              <br/><br/>
              <el-form-item label="上级类型"></el-form-item>
              <el-form-item label="资产类型" prop="relationship">
                <el-select :disabled="deviceInfoForm.$type !== 'add'" v-model="deviceInfoForm.relationship" @input="deviceInfoForm.parent = linearCITypeList.find(v => v.name === deviceInfoForm.relationship)" size="mini" class="form-item">
                  <el-option v-for="(item, index) in linearCITypeList.filter(v => v.level < 3)" :key="index + 'relationship'" :label="item.title" :value="item.name">
                    <div :class="'option-level' + item.level">{{item.title}}</div>
                  </el-option>
                </el-select>
              </el-form-item>
              <br/><br/>
              <template v-if="deviceInfoForm.parent.level === 2">
                <el-form-item label="基础属性"></el-form-item>
                <div v-for="(item, index) in deviceInfoForm.basicInfo" :key="index + 'basicInfo'" >
                  <el-form-item label="英文名" :prop="deviceInfoForm.basicInfo[index].disabled ? undefined : 'basicInfo-name' + (index - defaultBasicProps.length)" class="dib" style="vertical-align:top;">
                    <el-input :disabled="deviceInfoForm.basicInfo[index].disabled" v-model="deviceInfoForm.basicInfo[index].name" size="mini" class="form-item"></el-input>
                  </el-form-item>
                  <el-form-item label="中文名" :prop="deviceInfoForm.basicInfo[index].disabled ? undefined : 'basicInfo-title' + (index - defaultBasicProps.length)" class="dib" style="vertical-align:top;">
                    <el-input :disabled="deviceInfoForm.basicInfo[index].disabled" v-model="deviceInfoForm.basicInfo[index].title" size="mini" class="form-item"></el-input>
                  </el-form-item>
                  <el-form-item label="存储类型" :prop="deviceInfoForm.basicInfo[index].disabled ? undefined : 'basicInfo-type' + (index - defaultBasicProps.length)" class="dib" style="vertical-align:top;">
                    <el-select :disabled="deviceInfoForm.basicInfo[index].disabled" v-model="deviceInfoForm.basicInfo[index].type" size="mini" class="form-item">
                      <el-option v-for="(item, index) in ['bool', 'string', 'number']" :key="index" :value="item" :label="item">{{ item }}</el-option>
                    </el-select>
                  </el-form-item>

                  <span style="margin-left: 5px; vertical-align: -10px;">
                    <label v-if="deviceInfoForm.basicInfo.length > 1 && !deviceInfoForm.basicInfo[index].disabled"><a class="icomoon pic-sub" @click="deviceInfoForm.basicInfo.splice(index, 1)"></a></label>
                    <label v-if="deviceInfoForm.basicInfo.length - 1 === index"><a class="icomoon pic-add" @click="deviceInfoForm.basicInfo.push({})"></a></label>
                  </span>

                </div>
              </template>
              <br/><br/>
              <template v-if="deviceInfoForm.parent.level === 2">
                <el-form-item label="拓展属性"></el-form-item>
                <div v-for="(item, index) in deviceInfoForm.extendInfo" :key="index + 'extendInfo'" >
                  <el-form-item label="英文名" :prop="'extendInfo-name' + index" class="dib" style="vertical-align:top;">
                    <el-input v-model="deviceInfoForm.extendInfo[index].name" size="mini" class="form-item"></el-input>
                  </el-form-item>
                  <el-form-item label="中文名" :prop="'extendInfo-title' + index" class="dib" style="vertical-align:top;">
                    <el-input v-model="deviceInfoForm.extendInfo[index].title" size="mini" class="form-item"></el-input>
                  </el-form-item>
                  <el-form-item label="存储类型" :prop="'extendInfo-type' + index" class="dib" style="vertical-align:top;">
                    <el-select v-model="deviceInfoForm.extendInfo[index].type" size="mini" class="form-item">
                      <el-option v-for="(item, index) in ['bool', 'string', 'number']" :key="index" :value="item" :label="item">{{ item }}</el-option>
                    </el-select>
                  </el-form-item>

                  <span style="margin-left: 5px; vertical-align: -10px;">
                    <label v-if="deviceInfoForm.extendInfo.length > 1"><a class="icomoon pic-sub" @click="deviceInfoForm.extendInfo.splice(index, 1)"></a></label>
                    <label v-if="deviceInfoForm.extendInfo.length - 1 === index"><a class="icomoon pic-add" @click="deviceInfoForm.extendInfo.push({})"></a></label>
                    <label v-if="0 === index"><a class="icomoon pic-clear" @click="$set(deviceInfoForm.extendInfo, 0, {})"></a></label>
                  </span>
                </div>
              </template>
              <br/><br/>
              <template v-if="deviceInfoForm.parent.level === 2">
                <el-form-item label="连接关系"></el-form-item>
                <div v-for="(item, index) in deviceInfoForm.links" :key="index + 'links'">
                  <el-form-item label="资产类型" :prop="'links-child' + index" class="dib" style="vertical-align:top;">
                    <el-select v-model="deviceInfoForm.links[index].child" size="mini" class="form-item">
                      <el-option v-for="(item, index) in linearCITypeList" :key="index + 'relationship'" :label="item.title" :value="item.name" :disabled="item.level < 3">
                        <div :class="'option-level' + item.level">{{item.title}}</div>
                      </el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="连接关系" :prop="'links-type' + index" class="dib" style="vertical-align:top;">
                    <el-select v-model="deviceInfoForm.links[index].type" size="mini" class="form-item">
                      <el-option v-for="(item, index) in connectionTypeList" :key="index" :label="item.title" :value="item.name">
                        <div>{{item.title}}</div>
                      </el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="对应关系" :prop="'links-correspondence' + index" class="dib" style="vertical-align:top;">
                    <el-select v-model="deviceInfoForm.links[index].correspondence" size="mini" class="form-item">
                      <el-option v-for="(item, index) in ['一对一', '一对多', '多对一', '多对多']" :key="index" :label="item" :value="index">
                        <div>{{item}}</div>
                      </el-option>
                    </el-select>
                  </el-form-item>

                  <span style="margin-left: 5px; vertical-align: -10px;">
                    <label v-if="deviceInfoForm.links.length > 1"><a class="icomoon pic-sub" @click="deviceInfoForm.links.splice(index, 1)"></a></label>
                    <label v-if="deviceInfoForm.links.length - 1 === index"><a class="icomoon pic-add" @click="deviceInfoForm.links.push({})"></a></label>
                    <label v-if="0 === index"><a class="icomoon pic-clear" @click="deviceInfoForm.links[0] = {}"></a></label>
                  </span>
                </div>
              </template>

            </el-form>

          </div>
        </div>
      </el-tab-pane>
    </el-tabs>

    <el-dialog
      :title="addAssetForm.$type === 'add' ? '添加资产信息' : '修改资产信息'"
      class="m600"      
      :close-on-click-modal = "false"
      @close="addAssetVisible = false;$refs.addAsset.resetFields()"
      :visible="addAssetVisible">
      <el-form :model="addAssetForm" :rules="addAssetRules" label-width="150px" :disabled="addAssetForm.$type==='detail'" ref="addAsset" label-position="left" style="max-height: 550px; overflow-y:auto;">
        <el-form-item label="资产类型" prop="type">
          <el-select :disabled="addAssetForm.$type !== 'add'" v-model="addAssetForm.type" size="mini" class="form-item" style="width:340px" @input="getPorp4addForm()">
            <el-option v-for="(item, index) in linearCITypeList" :key="index + 'relationship'" :label="item.title" :value="item.name" :disabled="item.level < 3">
              <div :class="'option-level' + item.level">{{item.title}}</div>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="设备序列号" prop="sn">
          <el-input :disabled="addAssetForm.$type !== 'add'" :maxlength="30" v-model="addAssetForm.sn" size="mini" style="width:340px"></el-input>
        </el-form-item>
        <el-form-item label="地区编码" prop="address">
          <el-input v-model="addAssetForm.address" size="mini" style="width:340px"></el-input>
        </el-form-item>
        <el-form-item label="型号" prop="model">
          <el-input v-model="addAssetForm.model" size="mini" style="width:340px"></el-input>
        </el-form-item>
        <el-form-item label="状态" prop="status">
          <el-switch v-model="addAssetForm.status" active-text="正常" inactive-text="异常"></el-switch>
        </el-form-item>
        <el-form-item label="备注" prop="remarks">
          <el-input v-model="addAssetForm.remarks" size="mini" style="width:340px"></el-input>
        </el-form-item>
        
        <el-form-item v-if="addAssetForm.$type !== 'add'" label="ID" prop="id">
          <span>{{ addAssetForm.id }}</span>
        </el-form-item>
        <el-form-item v-if="addAssetForm.$type !== 'add'" label="企业编码" prop="enterpriseCode">
          <span>{{ addAssetForm.enterpriseCode | nullFilter }}</span>
        </el-form-item>
        <el-form-item v-if="addAssetForm.$type !== 'add'" label="服务编码" prop="serverCode">
          <span>{{ addAssetForm.serverCode | nullFilter }}</span>
        </el-form-item>
        <el-form-item v-if="addAssetForm.$type !== 'add'" label="网关服务编码" prop="gatewayServerCode">
          <span>{{ addAssetForm.gatewayServerCode | nullFilter }}</span>
        </el-form-item>
        <el-form-item v-if="addAssetForm.$type !== 'add'" label="创建时间" prop="createTime">
          <span>{{ addAssetForm.createTime | dateFormatFilter | nullFilter }}</span>
        </el-form-item>
        <el-form-item v-if="addAssetForm.$type !== 'add'" label="修改时间" prop="modifyTime">
          <span>{{ addAssetForm.modifyTime | dateFormatFilter | nullFilter }}</span>
        </el-form-item>
        <el-form-item v-if="addAssetForm.$type !== 'add'" label="创建人" prop="user">
          <span>{{ addAssetForm.user | nullFilter }}</span>
        </el-form-item>
        <template>
          <el-form-item v-for="(item, index) in addAssetForm.$props" :key="index + 'props'" :label="item.title">
            <el-input v-if="item.type !== 'bool'" v-model="addAssetForm[item.name]" size="mini" style="width:340px"></el-input>
            <el-switch v-else v-model="addAssetForm[item.name]"></el-switch>
          </el-form-item>
        </template>
        <el-form-item v-if="addAssetForm.type" label="建立连接关系">
          <el-switch v-model="addAssetForm.ifAddRelationship"></el-switch>
        </el-form-item>
        <template v-if="addAssetForm.ifAddRelationship && addAssetForm.type">
          <el-form-item :prop="'relationShip' + index" label-width="0" v-for="(item, index) in addAssetForm.relationships" :key="index + 'rs'">
            资产ID 
            <el-select size="mini" style="width:220px; margin: 0 10px;"
              v-model="addAssetForm.relationships[index].id2"
              filterable
              remote
              @input="addFormLinkIdChanged(index)"
              reserve-keyword
              placeholder="请输入关键词"
              :remote-method="queryAssets4addForm"
              :loading="addAssetForm.$queryLoading">
              <el-option
                v-for="item in (addAssetForm.$queryOptions || [])"
                :key="item.id"
                :label="item.id"
                :value="item.id">
              </el-option>
            </el-select>
            连接类型 
            <el-select v-model="addAssetForm.relationships[index].type" size="mini" style="width:120px; margin: 0 10px;">
              <el-option v-for="(item, oIndex) in addAssetForm.relationships[index].connectionList" :key="oIndex" :label="item.title" :value="item.type">{{ item.title }}</el-option>
            </el-select>

            <span style="margin-left: 5px; vertical-align: middle;">
              <label v-if="addAssetForm.relationships.length > 1"><a class="icomoon pic-sub" @click="addAssetForm.relationships.splice(index, 1)"></a></label>
              <label v-if="addAssetForm.relationships.length - 1 === index"><a class="icomoon pic-add" @click="addAssetForm.relationships.push({})"></a></label>
              <label v-if="0 === index"><a class="icomoon pic-clear" @click="addAssetForm.relationships[index].type = null;addAssetForm.relationships[index].id2 = null"></a></label>
            </span>
          </el-form-item>

        </template>
        
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" type="default" @click="addAssetVisible = false;$refs.addAsset.resetFields()">取 消</el-button>
        <el-button size="mini" type="primary" @click="addAssetSubmit()">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="拓扑图"
      id="topo-modal"
      :close-on-click-modal = "false"
      @close="topoVisible = false"
      :visible="topoVisible">

        <div class="topo-wrapper">
          <div class="topo-search">
            <el-input @keydown.native.enter="nodeFilter" v-model="topoQuery"></el-input>
          </div>
          <div id="topo"></div>
          <div class="color-sample-wrapper">
            <span class="color-sample" v-for="(item, index) in connectionTypeList" :key="index"><i :style="'background: '+item.color"></i>{{item.title}}</span>
          </div>
          <div id="popup-menu" v-show="!!showPopup" :style="{left: popupPos.x + 2 + 'px', top: popupPos.y + 98 + 'px'}" v-clickoutside="handleClickOutside">
            <template v-if="showPopup === true">
              <div class="menu-item" @click="popupMenuClick('addNode')">Add Node</div>
              <div class="menu-item" @click="popupMenuClick('addEdge')">Add Edge</div>
            </template>
            <div class="menu-item" v-if="showPopup === 'node'" @click="popupMenuClick('extend')">Extend</div>
            <div class="menu-item" v-if="showPopup === 'node'" @click="popupMenuClick('detail')">Detail</div>
            <div class="menu-item" v-if="showPopup !== true" @click="popupMenuClick('delete')">Delete</div>
          </div>
        </div>
    </el-dialog>
  </section>
</template>

<script>
import vis from 'vis-network'
import Clickoutside from 'element-ui/src/utils/clickoutside'
export default {
  name: 'assetsManage',
  directives: { Clickoutside },
  data () {
    return {
      enterpriseCode: '1',
      serverCode: '1',
      user: 'chenht',
      nodes: [{
        title: '资产类型',
        name: null,
        open: true,
        level: 0,
        children: []
      }],      
      treeSetting: {
        view: {
          showIcon: false,
          showLine: false,
          showTitle: false,
          dblClickExpand: false,
          fontCss: function(treeId, node) {
            return { color: "#fff" }
          },
          addDiyDom: (_, treeNode) => {
            if (treeNode.level > 2) { return; }
            let aObj = document.getElementById(treeNode.tId + "_a");
            let editStr = document.createElement('div');
            editStr.className = 'add-sub-device'
            editStr.innerHTML = '<a class="icomoon pic-add"></a>'
            aObj.append(editStr);
            aObj.parentNode.style.position = 'relative';
            editStr.addEventListener('click', e => {
              e.stopPropagation();
              this.resetDeviceInfoForm()
              this.$set(this.deviceInfoForm, 'relationship', treeNode.name)
              this.$set(this.deviceInfoForm, 'parent', treeNode)
            })
          }
        },
        data: {
          key: {
            children: "children",
            name: "title",
            title: "code"
          }
        },
        callback: {
          onClick: (event, treeId, treeNode) => {
            this.setDeviceInfoForm(treeNode);
          },
        }
      },
      curView: '1',
      deviceInfoForm: {
        children: [{}],
        basicInfo: [{}],
        extendInfo: [{}],
        links: [{}],
        parent: {},
      },
      connectionTypeList: [],
      assetsQuery: {},
      assetsList: [],
      pagination: {
        page: 1,
        pageSize: 15,
        total: 0,
        handleCurrentChange: (v) => {
          this.pagination.page = v;
          this.getAssetsList();
        },
        pageSizeChange: () => {
          this.pagination.pageSize = v;
          this.getAssetsList();
        },
      },
      addAssetVisible: false,
      addAssetForm: { relationships: [{}], $type: 'add' },
      defaultBasicProps: [
        {title: 'ID', name: 'id', type: 'string', disabled: true},
        {title: '企业编码', name: 'enterpriseCode', type: 'string', disabled: true},
        {title: '服务编码', name: 'serverCode', type: 'string', disabled: true},
        {title: '地区编码', name: 'address', type: 'string', disabled: true},
        {title: '网关服务编码', name: 'gatewayServerCode', type: 'string', disabled: true},
        {title: '类型', name: 'type', type: 'string', disabled: true},
        {title: '序列号', name: 'sn', type: 'string', disabled: true},
        {title: '型号', name: 'model', type: 'string', disabled: true},
        {title: '状态', name: 'status', type: 'bool', disabled: true},
        {title: '创建时间', name: 'createTime', type: 'number', disabled: true},
        {title: '修改时间', name: 'modifyTime', type: 'number', disabled: true},
        {title: '创建人', name: 'user', type: 'string', disabled: true},
        {title: '备注', name: 'remarks', type: 'string', disabled: true},
      ],
  
      showPopup: false,
      popupPos: {x: 0, y: 0},
      topoVisible: false,
      topoQuery: '',
      nodeIdMap: [],
    }
  },
  computed: {
    linearCITypeList () {
      let list = []
      const traversal = node => {
        list.push(node);
        node.children && node.children.forEach(traversal)
      }
      traversal(this.nodes[0])
      return list
    },
    deviceInfoRules () {
      let required = {
        required: true,
        message: '请输入',
        trigger: 'blur'
      };
      let rule = {
        name: [
          required, 
          {
            validator: (rule, value, callback) => {
              if (/[\u0100-\uFFFF]/.test(value)) { return callback(new Error('英文名不能有非英文字符')) }
              callback()
            }, 
            trigger: 'blur'
          }
        ],
        title: [required],
        code: [required],
      }
      

      new Array(20).fill('').forEach((v, index) => {    
        let i = index + this.defaultBasicProps.length;
        let required = (name1, name2) => ({
          validator: (r, v, callback) => {
            let value = this.deviceInfoForm[name1][i][name2];
            value ? callback() : callback(new Error('请输入'))
          },
          trigger: 'blur'
        })
        rule['basicInfo-name' + index] = [required('basicInfo', 'name'), {
          validator: (rule, _, callback) => {
            let value = this.deviceInfoForm.basicInfo[i].name;
            if (/[\u0100-\uFFFF]/.test(value)) { return callback(new Error('英文名不能有非英文字符')) }
            if (value.indexOf('$') > -1) { return callback(new Error('英文名不能有"$"字符')) }
            callback()
          },
          trigger: 'blur',
        }]
        rule['basicInfo-title' + index] = [required('basicInfo', 'title')];
        rule['basicInfo-type' + index] = [required('basicInfo', 'type')];
      })
      
      new Array(20).fill('').forEach((v, index) => {    
        let required = (name1, name2) => ({
          validator: (r, v, callback) => {
            let value = this.deviceInfoForm[name1][index][name2];
            (index === 0 && !this.deviceInfoForm[name1][0].name && !this.deviceInfoForm[name1][0].title && !this.deviceInfoForm[name1][0].type) || value ? 
              callback() : callback(new Error('请输入'))
          },
          trigger: 'blur'
        })
        rule['extendInfo-name' + index] = [required('extendInfo', 'name'), {
          validator: (rule, _, callback) => {
            let value = this.deviceInfoForm.extendInfo[index].name || '';
            if (/[\u0100-\uFFFF]/.test(value)) { return callback(new Error('英文名不能有非英文字符')) }
            if (value.indexOf('$') > -1) { return callback(new Error('英文名不能有"$"字符')) }
            callback()
          },
          trigger: 'blur',
        }]
        rule['extendInfo-title' + index] = [required('extendInfo', 'title')];
        rule['extendInfo-type' + index] = [required('extendInfo', 'type')];
      })

      new Array(20).fill('').forEach((v, index) => {    
        let required = (name1, name2) => ({
          validator: (r, v, callback) => {
            let value = this.deviceInfoForm[name1][index][name2];
            (index === 0 && !this.deviceInfoForm[name1][0].child && !this.deviceInfoForm[name1][0].type && !this.deviceInfoForm[name1][0].correspondence) || value ? 
              callback() : callback(new Error('请选择'))
          },
          trigger: 'blur'
        })
        rule['links-child' + index] = [required('links', 'child')];
        rule['links-type' + index] = [required('links', 'type')];
        rule['links-correspondence' + index] = [required('links', 'correspondence')];
      })

      return rule;
    },
    addAssetRules () {
      let rule = {
        type: [{
          required: true,
          message: '请选择',
          trigger: 'blur'
        }],
        sn: [{    
          required: true,
          message: '请输入',
          trigger: 'blur'
        }, {
          validator (rule, value, callback) {
            if (/[\u0100-\uFFFF]/.test(value)) { return callback(new Error(' ')) }
            callback()
          },
          message: 'SN不能有非英文字符',
          trigger: 'blur'
        }]
      };
      
      new Array(20).fill('').forEach((v, index) => {
        rule['relationShip' + index] = [{
          validator: (rule, value, callback) => {
            value = this.addAssetForm.relationships[index];
            if (value.id2 && value.type) {
              return callback()
            } else if (!value.id2 && !value.type && this.addAssetForm.relationships.length <= 1) {
              return callback()
            } else if (!value.id2) {
              return callback(new Error('请选择资产id'))
            } else {
              return callback(new Error('请选择链接类型'))
            }
          },
          trigger: 'blur',
        }]
      })
      return rule;
    }
  },
  methods: {
    getAssetsList () {
      let query = Object.assign({
        enterpriseCode: this.enterpriseCode,
        serverCode: this.serverCode,
        curPage: this.pagination.page,
        pageNum: this.pagination.pageSize,
      }, this.assetsQuery);

      query.startCreateTime ? (query.startCreateTime = query.startCreateTime.getTime()) : (query.startCreateTime = undefined);
      query.endCreateTime ? (query.endCreateTime = query.endCreateTime.getTime()) : (query.endCreateTime = undefined);
      query.startModifyTime ? (query.startModifyTime = query.startModifyTime.getTime()) : (query.startModifyTime = undefined);
      query.endModifyTime ? (query.endModifyTime = query.endModifyTime.getTime()) : (query.endModifyTime = undefined);

      this.$api.CISearch(query).then(res => {
        this.assetsList = res.infos;
        res.infos.forEach(v => {
          v.title = (this.linearCITypeList.find(u => u.name === v.type) || {title: v.type}).title
        })
        this.pagination.total = res.count;
      })
    },
    getDeviceInfoList () {
      return this.$api.CITypeSearch({
        title: '',
        name: '',
        code: '',
        level: 0
      }).then(res => {
        res.forEach(v => v.open = true);
        let root = {children: res.filter(v => v.level === 1)};

        const traversal = (node, source) => {
          let children = node.children;
          node.children = [];
          children.forEach(v => {
            v = res.find(u => u.name === v.name);
            traversal(v, node)
          })
          if (source) { source.children.push(node); }
        };
        traversal(root);
        this.nodes[0].children = root.children;
      })
    },
    getAllConnectionType () {
      this.$api.CIConnectionTypeSearch().then(res => {
        let colors = [
          '#FF0000',
          '#9F35FF',
          '#02C874',
          '#FFDC35',
          '#0066CC',
          '#9D9D9D',
          '#EA7500',
        ]
        res.forEach((v, i) => v.color = colors[i])
        this.connectionTypeList = res;
      })
    },
    resetDeviceInfoForm () {
      this.$refs.deviceInfo.resetFields();
      this.deviceInfoForm = {
        children: [{}],
        basicInfo: [{}],
        extendInfo: [{}],
        links: [{}],
        parent: {},
        relationship: null,
      }
      this.deviceInfoForm.$type = 'add'
      this.$set(this.deviceInfoForm, 'basicInfo', Object.assign([], this.defaultBasicProps));
    },
    setDeviceInfoForm (deviceInfo) {
      this.deviceInfoForm = {
        title: deviceInfo.title,
        name: deviceInfo.name,
        code: deviceInfo.code,
        relationship: deviceInfo.getParentNode().name,
        parent: deviceInfo.getParentNode(),
      }
      this.deviceInfoForm.$type = 'modify'

      this.$api.CIPropSearch({
        name: deviceInfo.name,
        enterpriseCode: this.enterpriseCode,
        serverCode: this.serverCode
      }).then(res => {
        let ei = res.props.find(v => v.enterpriseCode === this.enterpriseCode && v.serverCode === this.serverCode);
        let bi = res.props.find(v => !('serverCode' in v));
        this.deviceInfoForm.hasBasicInfo = !!bi;
        this.deviceInfoForm.hasExtendInfo = !!ei;
        let bProp = (bi || {
          title: [], name: [], type: []
        });
        bi = []
        bProp.title.forEach((_, i) => {
          if (this.defaultBasicProps.find(v => v.name === bProp.name[i])) {
            return;
          }
          bi.push({
            title: bProp.title[i],
            name: bProp.name[i],
            type: bProp.type[i],
          })
        })
        bi = Object.assign([], this.defaultBasicProps).concat(bi);
        this.$set(this.deviceInfoForm, 'basicInfo', bi);
        
        let eProp = (ei || {
          title: [undefined], name: [undefined], type: [undefined]
        });
        ei = []
        eProp.title.forEach((_, i) => {
          ei.push({
            title: eProp.title[i],
            name: eProp.name[i],
            type: eProp.type[i],
          })
        })
        this.$set(this.deviceInfoForm, 'extendInfo', ei);

        this.$api.CITypeSearchConnection({
          parent: this.deviceInfoForm.name
        }).then(res => {
          res.forEach(v => {
            v.child = v.name
          })
          this.$set(this.deviceInfoForm, 'links', res.length > 0 ? Object.assign([], res) : [{}]);
          this.deviceInfoForm.originLinks = res;
        })
      })
      // this.$api.CIPropSearch({
      //   name: deviceInfo.name,
      //   enterpriseCode: this.enterpriseCode,
      //   serverCode: this.serverCode

      // }).then(res => {
        
      //   console.log(res);
      // })
    },
    addDeviceInfo () {
      this.$refs.deviceInfo.validate(valid => {
        if (!valid) { return; }

        !this.deviceInfoForm.relationship && (this.deviceInfoForm.relationship = null)
        let parent = this.linearCITypeList.find(v => v.name === this.deviceInfoForm.relationship)
        let query = { 
          title: this.deviceInfoForm.title,
          name: this.deviceInfoForm.name,
          code: this.deviceInfoForm.code,
          level: parent.level + 1,
          relationship: parent.name
        }
        let method = this.deviceInfoForm.$type === 'add' ? 'CITypeAdd' : 'CITypeModify';
        this.$api[method](query).then(res => {
          (parent.level === 2 ? this.setProp() : {then: cb => cb()}).then(() => {
            this.getDeviceInfoList()
            this.resetDeviceInfoForm()
          })
        })
      })
    },
    setProp () {
      let basicInfo = !this.deviceInfoForm.basicInfo[0].name ? null : this.deviceInfoForm.basicInfo.reduce((o, n) => {
        o.title.push(n.title);
        o.type.push(n.type);
        o.name.push(n.name);
        return o
      }, { title: [], name: [], type: [] })
      let method = this.deviceInfoForm.hasBasicInfo ? 'CIPropModify' : 'CIPropAdd'
      basicInfo && this.$api[method]({
        name: this.deviceInfoForm.name,
        prop: basicInfo
      })
      
      let extendInfo = !this.deviceInfoForm.extendInfo[0].name ? null : this.deviceInfoForm.extendInfo.reduce((o, n) => {
        o.title.push(n.title);
        o.type.push(n.type);
        o.name.push(n.name);
        return o
      }, { title: [], name: [], type: [] })
      extendInfo && (extendInfo.enterpriseCode = this.enterpriseCode);
      extendInfo && (extendInfo.serverCode = this.serverCode);
      
        
      method = this.deviceInfoForm.hasExtendInfo ? 'CIPropModify' : 'CIPropAdd'
      extendInfo && this.$api[method]({
        name: this.deviceInfoForm.name,
        prop: extendInfo,
      })

      let originLinks = this.deviceInfoForm.originLinks || [];
      let links = this.deviceInfoForm.links.filter(v => v.child);
      let adds = links.filter(v => !originLinks.find(u => u.child === v.child && v.correspondence === u.correspondence && v.type === u.type));
      let deletes = originLinks.filter(v => !links.find(u => u.child === v.child && v.correspondence === u.correspondence && v.type === u.type));
      return new Promise(rev => {
        let counter = -1;
        const cb = () => {
          ++counter === deletes.length && rev();
        }
        cb();
        deletes.forEach(v => {
          this.$api.CITypeDeleteConnection({
            parent: this.deviceInfoForm.name,
            child: v.child,
            type: v.type,
            correspondence: v.correspondence
          }).then(cb)
        })
      }).then(() => new Promise(rev => {
        let counter = -1;
        const cb = () => {
          ++counter === adds.length && rev();
        }
        cb();
        adds.forEach(v => {
          this.$api.CITypeAddConnection({
            parent: this.deviceInfoForm.name,
            child: v.child,
            type: v.type,
            correspondence: v.correspondence
          }).then(cb)
        })
      }))
    },
    deleteDeviceInfo () {
      this.$confirm(`确定要删除选中的硬件吗？<br/>注意：该内容删除之后无法恢复！`, '是否删除', 
      { type: 'warning', dangerouslyUseHTMLString: true }).then(res => {
        this.$api.CITypeDelete({name: this.deviceInfoForm.name}).then(res => {
          this.getDeviceInfoList();
          this.resetDeviceInfoForm()
        })
      }).catch((e) => {
      })
    },

    addAssetToModal (asset, isDetail) {
      this.addAssetVisible = true;
      this.$nextTick(() => {
        if (asset) {  
          this.addAssetForm = Object.assign({}, asset);
          this.addAssetForm.$type = isDetail ? 'detail' : 'modify'
        } else {
          this.addAssetForm = {
            relationships: [{}],
          };
          this.addAssetForm.$type = 'add'
        }
        this.getPorp4addForm().then(() => {
          if (asset) {  
            this.getRelationships4Form();
          }

        });
      })
    },
    addAssetSubmit () {
      this.$refs.addAsset.validate(valid => {
        if (!valid) { return; }

        let query = {};
        let basic = ['type', 'id', 'sn', 'address', 'model', 'status', 'remarks'].map(v => ({name: v}))
        basic.concat(this.addAssetForm.$props).forEach(v => {
          query[v.name] = this.addAssetForm[v.name]
        })
        Object.assign(query, {
          enterpriseCode: this.enterpriseCode,
          serverCode: this.serverCode,
        })
        let method = this.addAssetForm.$type === 'add' ? 'CIAdd' : 'CIModify'
        this.$api[method](query).then(res => {
          this.getAssetsList();
          if (this.addAssetForm.$type === 'add') { this.addAssetForm.id = res.id; }
        }).then(() => {
          let relationships = this.addAssetForm.relationships.filter(v => v.type);
          let originRelationships = this.addAssetForm.originRelationships || [];

          if (!relationships) {
            relationships = [];
          }

          let adds = relationships.filter(v => !originRelationships.find(u => u.type === v.type && v.id2 === u.id2));
          let deletes = originRelationships.filter(v => !relationships.find(u => u.type === v.type && v.id2 === u.id2));

          new Promise(rev => {
            let counter = -1;
            const cb = () => {
              ++counter === deletes.length && rev();
            }
            cb();
            deletes.forEach(v => {
              this.$api.CIDeleteRelationship({
                id1: this.addAssetForm.id,
                id2: v.id2,
                type: v.type,
              }).then(cb)
            })
          }).then(() => new Promise(rev => {
            let counter = -1;
            const cb = () => {
              ++counter === adds.length && rev();
            }
            cb();
            adds.forEach(v => {
              this.$api.CIAddRelationship({
                id1: this.addAssetForm.id,
                id2: v.id2,
                type: v.type,
              }).then(cb)
            })
          })).then(() => {
            this.addAssetVisible = false;
            
            this.$message({
              message: this.addAssetForm.$type === 'add' ? '添加成功' : '修改成功',
              type: 'success',
            });
          })
        })
      })
     

    },
    getRelationships4Form () {
      this.$api.CISearchRelationship({
        id: this.addAssetForm.id,
        enterpriseCode: this.enterpriseCode,
        serverCode: this.serverCode,
      }).then(res => {
        let list = res.children;
        list.forEach(v => {
          v.id2 = v.id; 
          v.id1 = this.addAssetForm.id;
          v.connectionList = this.addAssetForm.$allConnections.filter(u => u.name === v.type)
          v.type = v.relationshipType;
        })
        this.$set(this.addAssetForm, 'relationships', list.map(v => Object.assign({}, v)));
        this.addAssetForm.originRelationships = list;
        if (!this.addAssetForm.relationships.length) { 
          this.$set(this.addAssetForm, 'relationships', [{}]) 
        } else {
          this.addAssetForm.ifAddRelationship = true;
        }
      })
    },
    getPorp4addForm () {
      let counter = 2;
      let rev = null
      if (!this.addAssetForm.type) { return {then: cb => cb()} }
      
      this.$api.CIPropSearch({
        name: this.addAssetForm.type,
        enterpriseCode: this.enterpriseCode,
        serverCode: this.serverCode
      }).then(res => {
        let list = []
        res.props.forEach(v => {
          v.title.forEach((_, i) => {
            !this.defaultBasicProps.find(u => u.name === v.name[i]) &&
            list.push({
              title: v.title[i],
              name: v.name[i],
              type: v.type[i],
            })
          })
        })
        this.$set(this.addAssetForm, '$props', list);
        if (!(--counter)) { rev() }
      })
      this.addAssetForm.$curType = this.linearCITypeList.find(v => v.name === this.addAssetForm.type);

      this.$api.CITypeSearchConnection({
        parent: this.addAssetForm.type
      }).then(res => {
        res.forEach(v => {
          v.title = this.connectionTypeList.find(u => u.name === v.type).title;
        })
        
        this.$set(this.addAssetForm, '$allConnections', res)
        if (!(--counter)) { rev() }
      })

      return new Promise(__rev => {
        rev = __rev
      })

    },
    queryAssets4addForm (query) {
      this.addAssetForm.$queryLoading = true;
      this.$api.CISearch({
        curPage: 1,
        pageNum: 15,
        enterpriseCode: this.enterpriseCode,
        serverCode: this.serverCode,
        id: query,
      }).then(res => {
        this.$set(this.addAssetForm, '$queryOptions', res.infos);
        this.addAssetForm.$queryLoading = false;
      })
    },
    addFormLinkIdChanged (index) {
      let id2 = this.addAssetForm.$queryOptions.find(v => v.id === this.addAssetForm.relationships[index].id2) || {};
      let id1 = this.addAssetForm.$curType;
      this.$set(this.addAssetForm.relationships[index], 'type',null)
      this.$set(this.addAssetForm.relationships[index], 'connectionList', this.addAssetForm.$allConnections.filter(v => v.name === id2.type));
    },
    
    deleteAsset (asset) {
      this.$confirm(`确定要删除选中的硬件吗？<br/>注意：该内容删除之后无法恢复！`, '是否删除', 
      { type: 'warning', dangerouslyUseHTMLString: true }).then(res => {
        this.$api.CIDelete({id: asset.id}).then(res => {
          this.getAssetsList();
        })
      }).catch(() => {

      })      
    },

    nodeFilter () {
      this.networkNodes.refresh();
    },
    handleClickOutside () {
      this.showPopup = false;
    },
    getCITypeColor (name) {
      let color = name.split('').reduce((o, n) => {
        let res = o * n.charCodeAt(0)
        if (res >= 2 ** 24) { res = res % (2 ** 24) }
        if (!res) { res = 1; }
        return res;
      }, 1)
      color = '000000' + color.toString(16);
      color = color.substr(color.length - 6)
      return '#' + color
    },
    popupMenuClick (op) {
      switch (op) {
        case 'addNode': {
          this.network.addNodeMode();
          break;
        }
        case 'addEdge': {
          this.network.addEdgeMode();
          break;
        }
        case 'detail': {
          if (!this.topoSelectedNode.loaded) {
            this.$api.CISearch({
              id: this.topoSelectedNode.data.id,
              enterpriseCode: this.enterpriseCode,
              serverCode: this.serverCode,
              pageNum: 1,
              curPage: 1
            }).then(res => {
              this.topoSelectedNode.data = res.infos[0]
              this.topoSelectedNode.loaded = true;
              this.addAssetToModal(this.topoSelectedNode.data, 'detail')
            })
          } else {
            this.addAssetToModal(this.topoSelectedNode.data, 'detail')
          }
          break;
        }
        case 'extend': {
          if (!this.topoSelectedNode.loaded) {
            this.$api.CISearch({
              id: this.topoSelectedNode.data.id,
              enterpriseCode: this.enterpriseCode,
              serverCode: this.serverCode,
              pageNum: 1,
              curPage: 1
            }).then(res => {
              this.topoSelectedNode.data = res.infos[0]
              this.topoSelectedNode.loaded = true;
            })
          }

          this.$api.CISearchRelationship({
            id: this.topoSelectedNode.data.id,
            enterpriseCode: this.enterpriseCode,
            serverCode: this.serverCode,
          }).then(res => new Promise(rev => {
            let list = [];
            res.children.forEach(v => {
              v.isChildren = true;
              v.loaded = false;
              list.push(v);
            })
            res.parent.forEach(v => {
              v.isParent = true;
              v.loaded = false;
              list.push(v)
            })
            list.forEach(v => {
              let node = this.nodeIdMap.find(u => u.label === v.id)
              if (!node) { 
                node = {
                  id: this.nodeIdMap.length,
                  data: v,
                  label: v.id,
                  color: this.getCITypeColor(v.type),
                  size: 35,
                  shape: 'dot',
                  font: {color: '#000', size: 14},
                  loaded: false,
                }
                this.nodeIdMap.push(node)
                this.networkNodes.getDataSet().add(node)
              }
              if (this.networkEdges.get({filter: (v) => {
                return v.from === this.topoSelectedNode.id && v.to === node.id ||
                  v.to === this.topoSelectedNode.id && v.from === node.id
              }}).length === 0) {
                let info = {
                  arrows: 'to',
                  color: {color: (this.connectionTypeList.find(u => u.name === v.relationshipType) || {color: 'black'}).color}
                }
                v.isParent ? 
                  this.networkEdges.getDataSet().add(Object.assign({
                    from: node.id,
                    to: this.topoSelectedNode.id,
                  }, info)) :
                  this.networkEdges.getDataSet().add(Object.assign({
                    from: this.topoSelectedNode.id,
                    to: node.id,
                  }, info))
              }
            })
            rev();
          }))

          break;
        }
      }
      this.showPopup = false;
    },
    getNewId () {
      return this.nodeMaxId++;
    },
    nodeFilter (node) {
      return node.id < +100;
    },
    draw () {
      let nodes = [];
      let edges = [];

      let node = {
        data: this.topoCurAsset,
        id: 0,
        label: this.topoCurAsset.id,
        color: this.getCITypeColor(this.topoCurAsset.type),
        size: 35,
        shape: 'dot',
        font: {color: '#000', size: 14},
        loaded: true,
      };
      let data = {
        nodes: new vis.DataView(new vis.DataSet([node]), { filter: this.nodeFilter }), 
        edges: new vis.DataView(new vis.DataSet([]), {
          filter: (edge) => {
            return true;
          }
        })
      };

      this.nodeIdMap = [node]
      this.nodeMaxId = 25;

      // create a network
      var container = document.getElementById('topo');
      var options = {
        layout: {randomSeed:2}, // just to make sure the layout is the same when the locale is changed
        physics:{
          maxVelocity: 10,
          barnesHut: {
            centralGravity: 0.1
          }
        },
        nodes: {
          shape: "dot",
          size: 30,
          font: {
            size: 32,
            color: "#ffffff"
          },
          borderWidth: 2
        },
        edges: {
          width: 2,
          arrows: "to"
        },
        manipulation: {
        //   addNode: function (data, callback) {
        //     // filling in the popup DOM elements
        //     document.getElementById('operation').innerHTML = "Add Node";
        //     document.getElementById('node-id').value = data.id;
        //     document.getElementById('node-label').value = data.label;
        //     document.getElementById('saveButton').onclick = saveData.bind(this, data, callback);
        //     document.getElementById('cancelButton').onclick = clearPopUp.bind();
        //     document.getElementById('network-popUp').style.display = 'block';
        //   },
        //   editNode: function (data, callback) {
        //     // filling in the popup DOM elements
        //     document.getElementById('operation').innerHTML = "Edit Node";
        //     document.getElementById('node-id').value = data.id;
        //     document.getElementById('node-label').value = data.label;
        //     document.getElementById('saveButton').onclick = saveData.bind(this, data, callback);
        //     document.getElementById('cancelButton').onclick = cancelEdit.bind(this,callback);
        //     document.getElementById('network-popUp').style.display = 'block';
        //   },
        //   addEdge: function (data, callback) {
        //     if (network.getConnectedNodes(data.from).indexOf(data.to) > -1) {
        //       return;
        //     }
        //     if (data.from == data.to) {
        //       return;
        //     }
        //     else {
        //       callback(data);
        //     }
        //   }
        }
      };
      let network = new vis.Network(container, data, options);
      network.on('oncontext', data => {
        data.event.preventDefault();
        this.popupPos = data.pointer.DOM
        this.showPopup = true;        
        if (data.nodes.length) { 
          this.showPopup = 'node'; 
          this.topoSelectedNode = this.nodeIdMap[this.network.getSelectedNodes()[0]];
        } else if (data.edges.length) { this.showPopup = 'edge'; }
        network.disableEditMode();
      })
      network.on('click', data => {
        console.log(data);
        this.showPopup = false;
      })

      this.networkNodes = data.nodes;
      this.networkEdges = data.edges;
      this.network = network;

    },
    topoToModal (asset) {
      this.topoVisible = true;
      this.topoCurAsset = asset;
      this.$nextTick(() => {
        this.draw();
      })

    }

  },
  mounted () {
    this.getDeviceInfoList().then(() => {
      this.getAssetsList()
    })
    this.resetDeviceInfoForm()
    this.getAllConnectionType()
  },
}
</script>

<style lang="less" scoped>
.tabs{
  flex: 1;
  height: calc(100% - 20px);
  margin: 10px;
}
.left-tree{
  width: 210px;
  padding: 10px;
  height: 100%;
  background-color: #0B1D3B;
}
.right-content{
  flex: 1;
  height: 100%;
  margin-left: 10px;
  background-color: #0B1D3B;
}
#deviceInfo{
  overflow-y: auto;
}
.list{
  flex: 1;
  overflow: hidden;
  padding-bottom: 10px;
  background-color: #0B1D3B;
  .table-box{
    flex: 1;
    overflow: hidden;
  }    
}
.option-level0{
  padding-left: 0px;
  margin: 0 -20px;
  background-color: transparent;
}
.option-level1{
  padding-left: 40px;
  margin: 0 -20px;
  background-color: rgba(255,255,255,0.1);
}
.option-level2{
  padding-left: 60px;
  margin: 0 -20px;
  background-color: rgba(255,255,255,0.15);
}
.option-level3{
  padding-left: 80px;
  margin: 0 -20px;
  background-color: rgba(255,255,255,0.2);
}
.topo-wrapper{
  width: 100%;
  height: 100%;
  background-color: #fff;
  position: relative;
}
#topo{
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  border: 1px solid #000;
}
#popup-menu{
  position: fixed;
  background-color: #fff;
  width: 100px;
  border: solid 1px #000;
  color: #000;
  .menu-item{
    padding: 5px;
    cursor: pointer;
    &:hover{
      background-color: #66f;
    }
  }
}
</style>
<style lang="less">
#assets-manage-page{
  .add-sub-device{
    position: absolute;
    right: 0;
    top: -2px;
    right: 0px;
  }
}
#deviceInfo .el-form-item{
  margin-bottom: 0px;
  width: 30%;
  max-width: 350px;
}
.dib{
  display: inline-block;
}
.form-item{
  width: 100%;
}
#topo-modal .el-dialog{
  margin: 5vh auto !important;
  width: 90%;
  height: 90%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  .el-dialog__body{
    flex: 1;
  }
  .topo-search{
    position: absolute;
    top: 1px;
    left: 1px;
    z-index: 2;
    input{
      background-color: #fff;
      border: solid 1px #666;
    }
  }
  .color-sample-wrapper{
    position: absolute;
    bottom: 10px;
    left: 0;
    z-index: 2;
    color: #000;
    .color-sample{
      i{
        display: inline-block;
        width: 20px;
        height: 14px;
        vertical-align: -3px;
        margin: 0 5px 0 15px;
      }
    }
  }
}
</style>


